<template>
  <form @submit.prevent="handleSubmit">
    <div class="form-item">
      <label>
        <span>账号:</span>
        <input type="text" placeholder="请输入账号" v-model="loginUser">
      </label>
      <p class="err">{{error.userName}}</p>
    </div>
    <div class="form-item">
      <label>
        <span>密码:</span>
        <input type="password" placeholder="请输入密码" v-model="loginPwd">
      </label>
      <p class="err">{{error.userPwd}}</p>
    </div>
    <div class="form-item">
      <button>登录</button>
    </div>
    <div class="register">
      还没有账号?快去 <RouterLink :to="{name:'Register'}">注册</RouterLink> 吧
    </div>
  </form>
</template>

<script>
  import {login} from "@/api/user";
  export default {
    data() {
      return {
        loginUser: '',
        loginPwd: '',
        error: {
          userName:'',
          userPwd: ''
        }
      }
    },
    methods: {
      async handleSubmit() {
        this.loginUser = this.loginUser.replaceAll(' ','');
        this.loginPwd = this.loginPwd.replaceAll(' ','');
        const rex =/[\u4e00-\u9fa5]/;
          if(rex.test(this.loginUser)) {
            this.error.userName = '账号不能包含汉字';
            return
          }
        if(rex.test(this.loginPwd)) {
          this.error.userPwd = '密码不能包含汉字';
          return
        }
        if(this.loginUser.length < 6 || this.loginUser.length > 10) {
          this.error.userName = '账号长度为6-10位';
          return
        }

        if(this.loginPwd.length < 6 || this.loginPwd.length > 16) {
          this.error.userPwd = '密码的长度为6-16位'
          return
        }
        this.error.userName = this.error.userPwd = '';
        const resp = await login(this.loginUser,this.loginPwd);
        console.log(resp)
        if(resp.code === 'error') {
          alert(resp.message)
          return
        }else {
          this.$router.push('/')
        }

      }
    }
  }
</script>

<style scoped lang="less">
form {

  .form-item {
    text-align: center;
    margin: 20px auto;
    span{
      margin-right: 10px;
    }
    input {
      border: 1px solid skyblue;
      width: 60%;
      height: 40px;
      border-radius: 5px;
      padding: 0 10px;
    }
  }
  button {
    width: 60%;
    height: 40px;
    text-align: center;
    background-color: #0a98c8;
    color: #fff;
    border-radius: 10px;
    &:hover {
      background-color: #52c6ed;
    }
  }
  .err {
    font-size: 12px;
    color: red;
    padding: 5px 0;
  }
  .register {
    text-align: center;
    font-size: 14px;
    color: gray;
    a {
      color: red;
    }
  }
}
</style>