<template>
  <div class="header-container">
    <div class="item left">
      <img src="@/assets/setting.png" alt="">
    </div>
    <form class="item center" @submit.prevent="handleSubmit">
      <div class="form-item">
        <input type="text" placeholder="请输入想要的商品" v-model="searchProduct">
      </div>
    </form>
    <div class="item right">
      <RouterLink :to="{name:'Login'}">登录</RouterLink>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        searchProduct: '',
      }
    },
   methods: {
     handleSubmit() {
       this.searchProduct = this.searchProduct.replaceAll(' ','');
       if(this.searchProduct.length <= 0) {
         return
       }
       this.$emit('search',this.searchProduct);
     },
   }
  }
</script>

<style scoped lang="less">
  @import "~@/styles/mixins.less";
.header-container {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 50px;
  background-color: red;
  display: flex;
  justify-content: space-between;
  border: 1px solid red;
}
.left {
  width: 50px;
  height: 50px;
 position: relative;
  img {
    width: 25px;
    height: 25px;
    cursor: pointer;
    .self-center();
  }
}
.center {
 flex: 1 1 auto;

  .form-item {
    width: 100%;
    height: 30px;
    margin-top: 10px;
    input {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      padding: 0 5px;
      &:focus{
        outline: 1px solid #000;
      }
    }
  }
}
  .right {
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
  }
</style>