<template>
  <form class="register-container" @submit.prevent="handleSubmit">
    <div class="form-item">
        <input type="text" placeholder="请输入您的账号" v-model="loginUser">
      <p class="err">{{error.userName}}</p>
    </div>
    <div class="form-item">
      <input type="password" placeholder="请输您的入密码" ref="inp" v-model="loginPwd">
      <span v-if="!showEye" class="hide" @click="handleClick"></span>
      <span v-else class="show" @click="handleClick"></span>
      <p class="err">{{error.userPwd}}</p>
    </div>
    <div class="form-item">
      <button>注册</button>
    </div>
  </form>
</template>

<script>
  import {register} from "@/api/user";
  export default {
    data() {
      return {
        showEye: false,
        loginUser:'',
        loginPwd: '',
        error: {
          userName: '',
          userPwd: ''
        }
      }
    },
    methods: {
      handleClick() {
        this.showEye = !this.showEye;
        if(!this.showEye) {
          this.$refs.inp.type = 'password';
        }else {
          this.$refs.inp.type = 'text';
        }
      },
      async handleSubmit() {
        this.loginUser = this.loginUser.replaceAll(' ','');
        this.loginPwd = this.loginPwd.replaceAll(' ','');
        const rex =/[\u4e00-\u9fa5]/;
        if(rex.test(this.loginUser)) {
          this.error.userName = '账号不能包含汉字';
          return
        }
        if(rex.test(this.loginPwd)) {
          this.error.userPwd = '密码不能包含汉字';
          return
        }
        if(this.loginUser.length < 6 || this.loginUser.length > 10) {
          this.error.userName = '账号长度为6-10位';
          return
        }

        if(this.loginPwd.length < 6 || this.loginPwd.length > 16) {
          this.error.userPwd = '密码的长度为6-16位'
          return
        }
        this.error.userName = this.error.userPwd = '';
        const resp = await register(this.loginUser,this.loginPwd);
        console.log(resp)
        if(resp.code === 'success') {
          alert('注册成功');
          this.$router.push('/login')
        }else {
          alert(resp.message)
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .register-container {
    text-align: center;
    margin: 50px 0;
    .form-item {
      margin: 30px 0;
      position: relative;
      input {
        width: 60%;
        height: 40px;
        border: 1px solid skyblue;
        padding: 0 5px;
        border-radius: 5px;

      }
      span.hide {
        display: inline-block;
        width: 16px;
        height: 16px;
        background: url("@/assets/hide.png") no-repeat;
        position: absolute;
        right: 90px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }
      span.show {
        display: inline-block;
        width: 32px;
        height: 32px;
        background: url("@/assets/show.png") no-repeat;
        position: absolute;
        right: 90px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }
    }
    button {
      width: 60%;
      height: 40px;
      text-align: center;
      background-color: #0a98c8;
      color: #fff;
      border-radius: 10px;
    }
    .err {
      font-size: 12px;
      color: red;
      padding: 5px 0;
    }
  }
</style>