import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "@/views/Home";
import Login from "@/views/Login";
import Detail from "@/views/Home/Detail";
import Register from "@/views/Register";
import Rotation from "@/views/Rotation";
import Card from "@/views/Card";
import Pleasantly from "@/views/Pleasantly";
Vue.use(VueRouter)

const routes = [
  {name:'Home',path:'/',component:Home},
  {name:'Login',path:'/login',component:Login},
  {name:'Detail',path:'/detail/:id',component: Detail},
  {name:'Register',path:'/register',component: Register},
  {name:'Rotation',path:'/rotation',component: Rotation},
  {name:'Card',path:'/card',component: Card},
  {name:'Pleasantly',path:'/pleasantly',component: Pleasantly},
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
