<template>
  <div class="home-container" ref="container">
    <Header @search="handleSearch"/>
    <ul class="product-info">
      <li v-for="it in list.products" :key="it.id">
        <div class="desc">
          <RouterLink :to="{name:'Detail',params:{id:it._id}}"><h3 class="title">{{it.name}}</h3></RouterLink>
          <p class="itDesc">{{it.descriptions || setPrice(it.price)}}</p>
        </div>
        <div class="imageInfo">
          <RouterLink :to="{name:'Detail',params:{id:it._id}}"><img :src="it.coverImg" ></RouterLink>
        </div>
      </li>
    </ul>
<!--    <Footer />-->
  </div>
</template>

<script>
  import {getList} from "@/api/cart";
  import Header from "@/components/Header";
  import ProductList from "./components/ProductList";
  import {setPrice} from "@/utils/setPrice";
  import Footer from "@/components/Footer";
  export default {
    components: {
      Header,
      ProductList,
      // Footer,
    },
    data() {
      return {
        list: {},
        page: 1,
        limit: 10,
      }
    },
    methods: {
      setPrice,
      async handleScroll() {
        const scrollheight = this.$refs.container.scrollHeight;
        const range = 1000;
        if(scrollheight - this.$refs.container.scrollTop <= range && this.page < this.list.pages) {
          this.page++;
          const resp = await getList(this.limit,this.page);
          this.list.products = this.list.products.concat(resp.products)
        }
      },
      async handleSearch(search) {
        this.list = await getList(this.limit,1,search);

      }
    },
    async created() {
      this.list = await getList(this.limit,this.page);
    },
    mounted() {
      this.$refs.container.addEventListener('scroll',this.handleScroll)
    },
    beforeDestroy() {
      this.$refs.container.removeEventListener('scroll',this.handleScroll)
    }
  }
</script>

<style scoped lang="less">
.home-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
ul {
  width: 100%;
  height: 100%;
  margin-top: 50px;
  li {
    width: 100%;
    margin: 10px 0;
    img {
      width: 100%;
      height: 100%;
    }
    .desc {
      text-align: center;
      cursor: pointer;
      a {
        display: block;
      }
      h3 {
        font-weight: bold;
        margin: 5px 0;
      }
      .itDesc {
        color: orange;
        font-size: 12px;
      }
    }
  }
}
}
</style>