<template>
  <div class="detail-container" v-if="data">
    <div class="list-content" v-html="data.content">
    </div>
  </div>
</template>

<script>
  import {getDetail} from "@/api/detail";

  export default {
    data() {
      return {
        data: null
      }
    },
    async created() {
      this.data = await getDetail(this.$route.params.id);
    }
  }
</script>

